._3Dr {
    max-width: 250px;
    min-width: 100px;
    min-height: 20px;
    padding-left: 10px;
    font-size: 14px;
    line-height: 20px;
    border-radius: 5px;
    background: rgba(0,0,0,.52);
    color:#fff;
    box-shadow: 0px 0px 1px 2px #fff;
    transition: background 200ms ease-in-out;
}
/* //这是一个测试 */
.zzj{
    position: absolute;
    width: 10px;
    height: 10px;
    background: rgba(0,0,0,.52);
    box-shadow: 0px 0px 0px 1px #fff;
    top: 7px;
    left: -10px;
    border-radius: 5px;
    cursor: pointer;
    transition:0.3s
}
.zzj.QVz,.zzj:hover{
    transform: scale(2)
}

._2kz{
    position:absolute;
    right: -22px;
    height: 20px;
    background:rgba(0,0,0,.52);
    border-radius: 5px;
    cursor: pointer;
}

._3lO{
    position:absolute;
    right:0px;
    width: 20px;
    height: 18px;
    transform: scale(1);
    background: #e6e6e6;
    border-radius: 25%;
    text-align:center;
    transition:.5s;
    cursor:pointer;
    box-shadow:0 2px 4px 0 rgba(0,0,0,.04)
}

._3ls{
    display: block;
    position:absolute;
    right:0;
    top:0;
    width: 8px;
    height: 8px;
    overflow: hidden;
    border-radius: 50%;
    text-align:center;
    transition:.5s;
    cursor:pointer;
}
._3ls.QVz{
    background: red;
}